import { Product } from "./interface";
import ProductItem from "./productitem"

export default function  ProductList(
    {props}:{
        props:{
            title:string,
            products:Product[]
        }
    }
){
    return(
        <div>
            <h1>{props.title}</h1>
            {
                props.products.map((p:Product)=>{
                    return(
                        <ProductItem Product={p}/>
                    );
                })
            }
        </div>
    )
}