async function getProducts(url:string){
  let res  = await fetch(url);
  let data = await res.json();
  let products = data.map((pr:any) => {
    return{
      id:pr.id,
      name: pr.name,
      price:pr.price,
      image:pr.image,
      description:"pr.description",
      category: pr.category
  }
  });
  return products;
}